.articlesCard {
  height: 560px;
  display: flex;
  flex-direction: column;
  background: #fff;
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 420ms cubic-bezier(0.165, 0.84, 0.44, 1);
}

.articlesCard:first-child {
  margin: 0;
}

.articlesCard:hover {
  transform: scale(1.05);
}

/* .articlesImageWrapper {
  width: 100%;
  height: 250px;
} */

.articlesImageWrapper img {
  width: 100%;
  height: 100%;
}

.articleBodyWrapper {
  padding: 30px 10px 10px 10px;
}

.articleBodyWrapper .articleCategory {
  font-style: normal;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 130%;
  color: #999999;
}

.articleBodyWrapper .articleTitle {
  margin-top: 20px;
  width: 80%;
  font-style: normal;
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 130%;
  color: #242424;
}

.articleFooterWrapper {
  margin-top: auto;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.articleFooterWrapper p {
  font-style: normal;
  font-weight: normal;
  font-size: 1.5rem;
  line-height: 130%;
  color: #1a61db;
}

/* Mediaqueries */

@media (max-width: 1024px) {
}

@media (max-width: 800px) {
}

@media (max-width: 500px) {
  .articlesCard {
    height: auto;
  }

  .articleBodyWrapper {
    padding: 20px 10px 10px 10px;
  }

  .articleBodyWrapper .articleCategory {
    font-size: 1rem;
  }

  .articleBodyWrapper .articleTitle {
    margin-top: 20px;
    width: 80%;
    font-size: 0.8rem;
  }

  .articleFooterWrapper {
    padding: 10px;
  }

  .articleFooterWrapper p {
    font-size: 0.6rem;
  }

  .articleFooterWrapper img {
    width: 18px;
  }
}
