.containermain {
    max-width: 768px;
  }
  
  .container {
    height: 480px;
    background-color: #00b87c;
    color: #ffffff;
    overflow: hidden;
  }
  
  .containerfluid {
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    color: #ffffff;
  }
  
  .zuritext {
    font-style: normal;
    font-weight: 800;
    font-size: 42px;
    line-height: 50px;
    color: #ffffff;
    padding-top: 30px;
  }
  
  .appstorediv {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000000;
    width: 95%;
    height: 55px;
    margin: 18px 0;
    border-radius: 5px;
  }
  
  .newtext {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #ffffff;
    text-decoration: underline;
  }
  
  .phonediv {
    display: flex;
    width: 90%;
    margin: auto;
    color: red;
  }
  
  .PhoneDM,
  .PhoneChannel {
    position: relative;
  }
  
  .PhoneDM {
    width: 310px;
    right: 23px;
    top: 90px;
  }
  
  .PhoneChannel {
    width: 310px;
    right: 280px;
    top: 60px;
  }
  
  .desktopdiv {
    background-color: #ffffff;
    padding: 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .desktopbigtext {
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    margin: 10px 0;
    padding-top: 20px;
    text-align: center;
  }
  
  .desktopsmalltext {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #616061;
    text-align: center;
    margin: 10px 0;
  }
  
  .emailinput {
    width: 90%;
    margin: 10px 0;
    padding: 20px;
    background-color: #ffffff;
    color: #bdbdbd;
    border: 1px solid #828282;
    border-radius: 5px;
    outline: none;
  }
  
  .btn {
    width: 100%;
    margin: 10px 0;
    padding: 20px;
    background-color: #00b87c;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    outline: none;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
  }